import jsPDF from "jspdf";
import "jspdf-autotable";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const LaporanPrintCashCard = (
  username = "",
  tanggal = "",
  validby = "",
  data
) => {
  // initialize jsPDF
  const doc = new jsPDF("landscape");
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];

  let tableColumn = [
    [
      {
        content: `TANGGAL`,
      },
      {
        content: `KETERANGAN`,
      },
      {
        content: `NO BON`,
      },
      {
        content: `SALDO AWAL`,
      },
      {
        content: `SALDO IN`,
      },
      {
        content: `SALDO OUT`,
      },
      {
        content: `SALDO AKHIR`,
      },
    ],
  ];
  let finalY = 40;
  doc.setFontSize(15);
  doc.text("LAPORAN KEUANGAN CASH - CARD", 14, 15);
  doc.setFontSize(10);
  data.forEach((item, index) => {
    let rows = [
      item.tanggal,
      item.keterangan,
      item.no_bon,
      parseFloat(item.saldo_awal).toLocaleString("id-ID").replace(".", ","),
      parseFloat(item.saldo_in).toLocaleString("id-ID").replace(".", ","),
      parseFloat(item.saldo_out).toLocaleString("id-ID").replace(".", ","),
      parseFloat(item.saldo_akhir).toLocaleString("id-ID").replace(".", ","),
    ];
    tableRows.push(rows);
  });
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    startY: 25,
    theme: "plain",
    rowPageBreak: "avoid",

    margin: { top: 20 },
    bodyStyles: { lineWidth: 0.02, lineColor: "#000" },
    headStyles: {
      lineWidth: 0.02,
      lineColor: "#000",
      fillColor: [212, 212, 211],
    },
    columnStyles: {
      2: { cellWidth: 40 },
      // etc
    },
  });
  finalY = doc.autoTableEndPosY() + 10;
  tableRows = [];

  doc.text(`User	: ${username}`, 14, finalY + 10);
  doc.text(`Cetak	: ${tanggal}`, 14, finalY + 16);
  doc.text(`Valid	: ${validby}`, 14, finalY + 22);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  doc.autoPrint();
  doc.setProperties({
    title: "LAPORAN KEUANGAN CASH - CARD",
  });
  // doc.save(`PenerimaanSUpplier.pdf`);
  var string = doc.output("datauristring");
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN KEUANGAN CASH - CARD</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default LaporanPrintCashCard;
